import { MSG_FETCH_CSRF_HEADERS_FAILED } from "../../utils/toastMessages";
import useToast from "../useToast";
import { fetchCsrfToken } from "../../services/api/PlantAssignment/fetchCsrfService";
import {  fetchData } from "../../utils/helpers";
import { useEffect, useState } from "react";

const useMassUpload = () => {
  const { showErrorToast } = useToast();
  const [mappedAttributes, setMappedAttributes] = useState([]);



// Function to Fetch Spreadsheet Column Mapping
const fetchColumnMapping = async () => {
  try {
    console.log("Fetching column mapping...");
    
    // Get CSRF Headers
    const headers = await fetchCsrfToken();
    if (!headers) {
      showErrorToast(MSG_FETCH_CSRF_HEADERS_FAILED);
      return;
    }

    // 🔹 Step 1: Call API 1 (GET) to Fetch Metadata
    const metadataResponse = await fetchData(
      "GET",
        "https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dseng/dseng:EngItem/search?%24top=1",
        null
    );

    if (!metadataResponse || !metadataResponse.data) {
      throw new Error("Failed to fetch metadata.");
    }
    console.log("Metadata Response:", metadataResponse.data);

    const objectId = metadataResponse.data?.member?.[0]?.id;
    if (!objectId) {
      throw new Error("No object ID found in metadata.");
    }

    console.log("Extracted Object ID:", objectId);

    // 🔹 Step 2: Call API 2 (POST) to Fetch Attributes
    const attributeResponse = await fetchData(
      "POST",
      "https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/collabServices/attributes/op/read?tenant=OI000186152&xrequestedwith=xmlhttprequest",
      {
        busIDs: [objectId],
      }
    );

    if (!attributeResponse || !attributeResponse.data) {
      throw new Error("Failed to fetch attribute data.");
    }

    const attributes = await attributeResponse.json();
    console.log("Attribute Response:", attributes);

    // 🔹 Step 3: Extract & Map Relevant Attributes
    const relevantAttributes =
    attributeResponse.data.results?.[0]?.groupData?.filter(
      (attr) => attr.deploymentExtension === true
    ) || [];

    const mappedData = relevantAttributes.map((attr) => ({
      uiLabel: attr.nls, // UI Display Name
      backendName: attr.name, // Backend Name
    }));

    console.log("Mapped Attributes:", mappedData);
    setMappedAttributes(mappedData);
    
  } catch (error) {
    console.error("Error fetching column mapping:", error);
    showErrorToast(error.message || "Error fetching column mapping.");
  }
};

useEffect(() => {
  fetchColumnMapping();
}, []);

return { mappedAttributes };
};

export default useMassUpload;
